import { getMagnificationAreaFactor } from '../canvasParameters/magnificationAreaFactor';


export default function drawingAlgorithm(event) {
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const defaultFrame = document.querySelector('.default-frame');
    const ct = defaultFrame.getContext('2d');
    const x = event.offsetX;
    const y = event.offsetY;
    const width = Math.floor(x / getMagnificationAreaFactor()) * getMagnificationAreaFactor();
    const height = Math.floor(y / getMagnificationAreaFactor()) * getMagnificationAreaFactor();
    ctx.fillRect(width, height, getMagnificationAreaFactor(), getMagnificationAreaFactor());
    ct.fillRect(width / 8, height / 8, getMagnificationAreaFactor() / 8, getMagnificationAreaFactor() / 8);
}
