let framesPixelsArray = [];
function getframesPixelsArray() {
    return framesPixelsArray;
}

function setframesPixelsArray(value, index) {
    const temp = getframesPixelsArray();
    temp[index] = value;
    framesPixelsArray = temp;
}

function deleteframesPixelsArray(index) {
    const temp = getframesPixelsArray();
    temp.splice(index, 1);
    framesPixelsArray = temp;
}

function pushframesPixelsArray(value) {
    const temp = getframesPixelsArray();
    temp.push(value);
    framesPixelsArray = temp;
}

export { getframesPixelsArray, setframesPixelsArray, deleteframesPixelsArray, pushframesPixelsArray };
