
import { setIntervalId } from './interval';

export default function animation() {
    const animationPreview = document.querySelector('.animation-preview-content');
    const context = animationPreview.getContext('2d');
    const frames = document.querySelectorAll('.frame');
    const contextList = [];
    frames.forEach((frame) => {
        const ctx = frame.getContext('2d');
        const imageData = ctx.getImageData(0, 0, frame.width, frame.height);
        contextList.push(imageData);
    });
    let i = 0;
    const intervalId = setInterval(() => {
        context.clearRect(0, 0, 112, 112);
        context.putImageData(contextList[i], 0, 0, 0, 0, 112, 112);
        i++;
        if (i === contextList.length) {
            i = 0;
        }
    }, 500);
    setIntervalId(intervalId);
}
