export default function selectingColor(event) {
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const selectedColor = document.querySelector('.selected-color');
    const prevColor = document.querySelector('.prev-color');
    const currentColor = selectedColor.style.backgroundColor;
    const x = event.offsetX;
    const y = event.offsetY;
    const imageData = ctx.getImageData(x, y, 1, 1);
    const pixel = imageData.data;
    const rgb = `rgb(${pixel[0]},${pixel[1]},${pixel[2]})`;
    selectedColor.style.backgroundColor = rgb;
    prevColor.style.backgroundColor = currentColor;
}
