import { getMagnificationAreaFactor } from './magnificationAreaFactor';

export default function showCoordinate() {
    const coordinateInfoContainer = document.querySelector('.coordinate-info');
    const canvas = document.querySelector('#canvas');
    canvas.addEventListener('mousemove', (event) => {
        const x = event.offsetX;
        const y = event.offsetY;
        const width = Math.floor(x / getMagnificationAreaFactor());
        const height = Math.floor(y / getMagnificationAreaFactor());
        coordinateInfoContainer.textContent = `x:${width}, y: ${height}, ${896 / getMagnificationAreaFactor()}*${896 / getMagnificationAreaFactor()}`;
    });
}
