import { getMagnificationAreaFactor } from '../canvasParameters/magnificationAreaFactor';

export default function dithering() {
    let ditheringOn = true;
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const defaultFrame = document.querySelector('.default-frame');
    const ct = defaultFrame.getContext('2d');
    const selectedColor = document.querySelector('.selected-color');
    const prevColor = document.querySelector('.prev-color');
    ctx.fillStyle = 'white';
    ct.fillStyle = 'white';
    canvas.addEventListener('mouseup', () => {
        ditheringOn = false;
    });
    canvas.addEventListener('mouseleave', () => {
        ditheringOn = false;
    });

    canvas.addEventListener('mousemove', (event) => {
        if (ditheringOn) {
            ctx.fillStyle = selectedColor.style.backgroundColor;
            ct.fillStyle = selectedColor.style.backgroundColor;
            const x = event.offsetX;
            const y = event.offsetY;
            const factor = getMagnificationAreaFactor();
            const width = Math.floor(x / factor) * factor;
            const height = Math.floor(y / factor) * factor;
            ctx.fillRect(width, height, factor / 2, factor / 2);
            ct.fillRect(width / 8, height / 8, factor / 16, factor / 16);
            ctx.fillRect(width + factor, height + factor, factor / 2, factor / 2);
            ct.fillRect((width + factor) / 8, (width + factor) / 8, factor / 8, factor / 8);
            ctx.fillStyle = prevColor.style.backgroundColor;
            ct.fillStyle = prevColor.style.backgroundColor;
            ctx.fillRect(width + factor, height, factor / 2, factor / 2);
            ct.fillRect((width + factor) / 8, height / 8, factor / 16, factor / 16);
            ctx.fillRect(width, height + factor, factor / 2, factor / 2);
            ct.fillRect(width / 8, (height + factor) / 8, factor / 16, factor / 16);
        }
    });
}
