import drawingByPen from './drawingByPen';
import eras from './erasing';
import selectingColor from './selectingColor';
import dithering from './dithering';
import doubleDrawingByPen from './doubleDrawingByPen';

export default function penDraw() {
    const pen = document.querySelector('.pen');
    const canvas = document.querySelector('#canvas');
    pen.addEventListener('click', () => {
        canvas.removeEventListener('click', selectingColor);
        canvas.removeEventListener('mousedown', eras);
        canvas.removeEventListener('mousedown', dithering);
        canvas.removeEventListener('mousedown', doubleDrawingByPen);
        canvas.addEventListener('mousedown', drawingByPen);
    });
}
