import { getframesPixelsArray } from './framesPixelsArray';
import frameChooser from './frameChooser';
import deleteFrame from './deleteButton';
import copyFrame from './copyFrame';
import animation from '../preview/animation';
import { getItervalId } from '../preview/interval';


export default function createCanvas() {
    const lengthOF = getframesPixelsArray().length;
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const framesContainer = document.querySelector('.frames-container');
    const buttonContainer = document.createElement('DIV');
    const deleteButton = document.createElement('DIV');
    const copyButton = document.createElement('DIV');
    buttonContainer.classList.add('buttons-container');
    deleteButton.classList.add('delete-button');
    deleteButton.textContent = 'Delete';
    copyButton.classList.add('copy-button');
    copyButton.textContent = 'Copy';
    const defaultFrame = document.createElement('CANVAS');
    defaultFrame.classList.add('default-frame');
    defaultFrame.classList.add('frame');
    defaultFrame.id = lengthOF;
    defaultFrame.width = 112;
    defaultFrame.height = 112;
    framesContainer.appendChild(buttonContainer);
    buttonContainer.appendChild(deleteButton);
    buttonContainer.appendChild(copyButton);
    framesContainer.appendChild(defaultFrame);
    copyFrame(copyButton, ctx);
    frameChooser(defaultFrame, ctx);
    deleteFrame(deleteButton);
    clearInterval(getItervalId());
    animation();
}
