import drawingAlgorithm from './drawingAlgorithm';

export default function drawingByPen(e) {
    let drawing = true;
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const defaultFrame = document.querySelector('.default-frame');
    const ct = defaultFrame.getContext('2d');
    const selectedColor = document.querySelector('.selected-color');
    ctx.fillStyle = 'white';
    ct.fillStyle = 'white';
    canvas.addEventListener('mouseup', () => {
        drawing = false;
    });
    canvas.addEventListener('mouseleave', () => {
        drawing = false;
    });
    drawingAlgorithm(e);
    canvas.addEventListener('mousemove', (event) => {
        if (drawing) {
            ctx.fillStyle = selectedColor.style.backgroundColor;
            ct.fillStyle = selectedColor.style.backgroundColor;
            drawingAlgorithm(event);
        }
    });
}
