import { deleteframesPixelsArray } from './framesPixelsArray';

export default function deleteFrame(deleteButton) {
    deleteButton.addEventListener('click', () => {
        const frameForDeleteing = deleteButton.parentNode.nextSibling;
        const frameContainer = frameForDeleteing.parentNode;
        frameContainer.removeChild(frameForDeleteing);
        frameContainer.removeChild(deleteButton.parentNode);
        const frameId = frameForDeleteing.id;
        deleteframesPixelsArray(frameId);
    });
}
