import { setMagnificationAreaFactor } from './magnificationAreaFactor';

export default function resizer() {
    const resizeTo32Button = document.querySelector('.area-32');
    const resizeTo64Button = document.querySelector('.area-64');
    const resizeTo128Button = document.querySelector('.area-128');

    const resizeTo32 = function resizeTo32() {
        setMagnificationAreaFactor(28);
    };

    const resizeTo64 = function resizeTo64() {
        setMagnificationAreaFactor(14);
    };

    const resizeTo128 = function resizeTo128() {
        setMagnificationAreaFactor(7);
    };

    resizeTo32Button.addEventListener('click', resizeTo32);
    resizeTo64Button.addEventListener('click', resizeTo64);
    resizeTo128Button.addEventListener('click', resizeTo128);
}
