import createCanvas from './frames';
import { getframesPixelsArray, pushframesPixelsArray } from './framesPixelsArray';


export default function copyFrame(copyButton, context) {
    copyButton.addEventListener('click', () => {
        const canvasForCopying = copyButton.parentNode.nextSibling;
        const canvasForCopyingId = canvasForCopying.id;
        let imageData;
        if (getframesPixelsArray().length === 0) {
            const canvas = document.querySelector('#canvas');
            imageData = context.getImageData(0, 0, canvas.width, canvas.height);
        } else {
            imageData = getframesPixelsArray()[canvasForCopyingId];
        }
        pushframesPixelsArray(imageData);
        const ct = canvasForCopying.getContext('2d');
        const smallimageData = ct.getImageData(0, 0, canvasForCopying.width, canvasForCopying.height);
        document.querySelector('.default-frame').classList.remove('default-frame');
        createCanvas();
        const currentCanvas = document.querySelector('.default-frame');
        const ct2 = currentCanvas.getContext('2d');
        ct2.putImageData(smallimageData, 0, 0);
        context.putImageData(imageData, 0, 0);
    });
}
