import { getMagnificationAreaFactor } from '../canvasParameters/magnificationAreaFactor';

export default function doubleDrawingByPen(e) {
    let doubleDrawing = true;
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const defaultFrame = document.querySelector('.default-frame');
    const ct = defaultFrame.getContext('2d');
    const selectedColor = document.querySelector('.selected-color');
    function doublePenDrawingAlgoritm(event) {
        ctx.fillStyle = selectedColor.style.backgroundColor;
        ct.fillStyle = selectedColor.style.backgroundColor;
        const x = event.offsetX;
        const y = event.offsetY;
        const factor = getMagnificationAreaFactor();
        const width = Math.floor(x / factor) * factor;
        const height = Math.floor(y / factor) * factor;
        const mirrorWidth = Math.floor((896 - x) / factor) * factor;
        ctx.fillRect(width, height, factor, factor);
        ct.fillRect(width / 8, height / 8, factor / 8, factor / 8);
        ctx.fillRect(mirrorWidth, height, factor, factor);
        ct.fillRect(mirrorWidth / 8, height / 8, factor / 8, factor / 8);
    }
    ctx.fillStyle = 'white';
    ct.fillStyle = 'white';
    canvas.addEventListener('mouseup', () => {
        doubleDrawing = false;
    });
    canvas.addEventListener('mouseleave', () => {
        doubleDrawing = false;
    });
    doublePenDrawingAlgoritm(e);
    canvas.addEventListener('mousemove', (event) => {
        if (doubleDrawing) {
            doublePenDrawingAlgoritm(event);
        }
    });
}
