import createCanvas from './frames';
import { pushframesPixelsArray } from './framesPixelsArray';

export default function addNewFrame() {
    const canvas = document.querySelector('#canvas');
    const ctx = canvas.getContext('2d');
    const addButton = document.querySelector('.add-button');
    addButton.addEventListener('click', () => {
        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        pushframesPixelsArray(imageData);
        const defaultFrame = document.querySelector('.default-frame');
        defaultFrame.classList.remove('default-frame');
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        createCanvas();
    });
}
