export default function palette() {
    const selectedColor = document.querySelector('.selected-color');
    const prevColor = document.querySelector('.prev-color');
    const blueColor = document.querySelector('.blue-color');
    const greenColor = document.querySelector('.green-color');
    const yellowColor = document.querySelector('.yellow-color');
    const redColor = document.querySelector('.red-color');
    blueColor.addEventListener('click', (e) => {
        const computedStyle = getComputedStyle(e.target);
        const currentColor = getComputedStyle(selectedColor).backgroundColor;
        prevColor.style.backgroundColor = currentColor;
        selectedColor.style.backgroundColor = computedStyle.backgroundColor;
    });
    greenColor.addEventListener('click', (e) => {
        const computedStyle = getComputedStyle(e.target);
        const currentColor = getComputedStyle(selectedColor).backgroundColor;
        prevColor.style.backgroundColor = currentColor;
        selectedColor.style.backgroundColor = computedStyle.backgroundColor;
    });
    yellowColor.addEventListener('click', (e) => {
        const computedStyle = getComputedStyle(e.target);
        const currentColor = getComputedStyle(selectedColor).backgroundColor;
        prevColor.style.backgroundColor = currentColor;
        selectedColor.style.backgroundColor = computedStyle.backgroundColor;
    });
    redColor.addEventListener('click', (e) => {
        const computedStyle = getComputedStyle(e.target);
        const currentColor = getComputedStyle(selectedColor).backgroundColor;
        prevColor.style.backgroundColor = currentColor;
        selectedColor.style.backgroundColor = computedStyle.backgroundColor;
    });
}
