import { getframesPixelsArray, setframesPixelsArray, pushframesPixelsArray } from './framesPixelsArray';

export default function frameChooser(frame, context) {
    console.log(getframesPixelsArray().length);
    frame.addEventListener('click', (e) => {
        const canvas = document.querySelector('#canvas');
        const prevFrame = document.querySelector('.default-frame');
        const prevFrameId = prevFrame.id;
        const choosenFrame = e.target;
        const nextFrameId = choosenFrame.id;
        const lastImageData = context.getImageData(0, 0, canvas.width, canvas.height);
        if (prevFrameId < getframesPixelsArray().length) {
            setframesPixelsArray(lastImageData, prevFrameId);
        } else {
            pushframesPixelsArray(lastImageData);
        }
        console.log(prevFrameId);
        console.log(getframesPixelsArray()[nextFrameId] === getframesPixelsArray()[0]);
        console.log(getframesPixelsArray());
        prevFrame.classList.remove('default-frame');
        choosenFrame.classList.add('default-frame');
        const imageData = getframesPixelsArray()[nextFrameId];
        context.putImageData(imageData, 0, 0);
    });
}
